import React from 'react';
export default class EventTime extends React.Component {
    render() {
        return(
            // make card
            <div className="card gradient_bg_card">
                <div className="card mx-3">
                    {/* card header */}
                    <div className="card-header">
                        <h3 className="text-dark font-weight-bold my-2">Event Rundown</h3>
                    </div>
                    {/* body */}
                    <div className="card-body">
                        <div className="d-flex align-items-center justify-content-center">
                            <div className="min-w-[130px]">
                                <div className="rounded-xl bg-peach p-2">
                                    <h5 className="time_rundown">08:00 - 19:20</h5>
                                </div>
                            </div>
                            <div className="flex-grow-1 text-left ml-4">
                                <h5 className="text-dark m-0 font-weight-bold">Opening Ceremony</h5>
                                <p className="text-muted m-0">Lorem ipsum dolor sit amet consectetur adipisicing elit. Quisquam, quae.</p>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}
    